import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import GroupsTable from './Tables/GroupsTable';
import GroupsModal from './Modals/GroupsModal'
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'

class Groups extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showdelete: false,
            GROUP_ID: 0,
            GROUP_NAME: '',
            rolesdata: [],
            grouprolesdata: [],
            checkedRoles: [],
            expandedRoles: [],
            filteredgrouprolesdata: [],
            allrolegroups: [],
            CREATED_BY: props.user.EMAIL,
            MODIFIED_BY: props.user.EMAIL,
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: ''
        }

        this.handleShow = () => {
            const expandedRoles = this.state.rolesdata.map(role => role.value)
            this.setState({
                show: true,
                grouprolesdata: this.state.rolesdata,
                expandedRoles,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false,
                checkedRoles: [],
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.populateData();

    }

    filterRolesTree = () => {

        // Reset nodes back to unfiltered state
        if (!this.state.roleFilterText) {
            this.setState((prevState) => ({
                filteredgrouprolesdata: this.state.grouprolesdata,
            }));

            return;
        }

        let filteredgrouproles = [];

        this.state.grouprolesdata.forEach(role => {
            if (role.label.toLocaleLowerCase().indexOf(this.state.roleFilterText.toLocaleLowerCase()) > -1) {
                filteredgrouproles.push(role);
            }
        })

        this.setState({
            filteredgrouprolesdata: filteredgrouproles
        });

        return filteredgrouproles;
    }

    populateData() {
        const getdata = `
            query {
                Groups {
                    GROUP_ID
                    GROUP_NAME
                    CREATED_BY
                    MODIFIED_BY
                    CREATED_DATE
                    MODIFIED_DATE
                },

                Roles {
                    ROLE_ID
                    ROLE_NAME
                },

                groupRoles {
                    GROUP_ROLE_ID
                    GROUP_ID
                    GROUP {
                        GROUP_ID
                        GROUP_NAME
                        CREATED_BY
                        MODIFIED_BY
                        CREATED_DATE
                        MODIFIED_DATE
                    },
                    ROLE {
                        ROLE_ID
                        ROLE_NAME
                    },
                    ACTIVE
                    READ_PERMISSION
                    CREATE_PERMISSION
                    UPDATE_PERMISSION
                    DELETE_PERMISSION
                    PRINT_PERMISSION
                    APPROVE_PERMISSION
                    CREATED_DATE
                    CREATED_BY
                    MODIFIED_DATE
                    MODIFIED_BY
                }

            }
        `;

        API.getData(getdata)
            .then(({ data }) => {

                const allrolegroups = data.data.groupRoles;
                const roleGroups = data.data.Groups;
                const roles = data.data.Roles;

                const rolesdata = [];
                const expandedRoles = [];

                roles.forEach(role => {

                    const roledetails = {
                        value: role.ROLE_ID,
                        label: role.ROLE_NAME,
                        children: [
                            {
                                value: `${role.ROLE_ID}-READ_PERMISSION`,
                                label: "Read",
                            },
                            {
                                value: `${role.ROLE_ID}-CREATE_PERMISSION`,
                                label: "Create",
                            },
                            {
                                value: `${role.ROLE_ID}-UPDATE_PERMISSION`,
                                label: "Update",
                            },
                            {
                                value: `${role.ROLE_ID}-DELETE_PERMISSION`,
                                label: "Delete",
                            },
                            {
                                value: `${role.ROLE_ID}-PRINT_PERMISSION`,
                                label: "Print",
                            },
                            {
                                value: `${role.ROLE_ID}-APPROVE_PERMISSION`,
                                label: "Approve",
                            },
                        ],
                    };

                    rolesdata.push(roledetails)
                    expandedRoles.push(role.ROLE_ID);
                });

                this.setState({
                    data: roleGroups,
                    allrolegroups,
                    rolesdata,
                    expandedRoles,
                })
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Group Data');
                }
            })

    }

    handleSubmit = (event) => {
        event.preventDefault();

        if (this.state.InsertUpdate === 'Insert') {

            const addGroupQuery = `
                mutation{
                    addGroup(
                        GROUP_NAME:"${this.state.GROUP_NAME}",
                        CREATED_BY:"${this.state.CREATED_BY}",
                        MODIFIED_BY:"${this.state.MODIFIED_BY}",
                        CREATED_DATE:"${this.state.CREATED_DATE}",
                        MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        GROUP_ID
                    }
                }
            `;

            API.create(addGroupQuery)
                .then(({ data }) => {

                    const GROUP_ID = data.data.addGroup.GROUP_ID;
                    const allRolesIds = this.state.rolesdata.map(role => role.value);
                    const groupRoles = [];
                    let checkedRolesIds = [];
                    let checked_roles = {};

                    this.state.checkedRoles.forEach(role => {
                        const splitrole = role.split('-');

                        if (splitrole[0] in checked_roles && Array.isArray(checked_roles[splitrole[0]])) {
                            checked_roles[splitrole[0]].push(splitrole[1]);
                        } else {
                            const roledetails = {};
                            roledetails[splitrole[0]] = [splitrole[1]];
                            checked_roles = { ...checked_roles, ...roledetails };
                        }

                    });

                    for (const role in checked_roles) {
                        if (checked_roles.hasOwnProperty(role)) {
                            checkedRolesIds.push(role);
                            groupRoles.push(
                                `
                                    {
                                        GROUP_ID: ${ GROUP_ID}, 
                                        ROLE_ID: ${ role}, 
                                        ACTIVE: ${ 1}, 
                                        READ_PERMISSION: ${ checked_roles[role].includes('READ_PERMISSION')===true?1:0},
                                        CREATE_PERMISSION: ${ checked_roles[role].includes('CREATE_PERMISSION')===true?1:0},
                                        UPDATE_PERMISSION: ${ checked_roles[role].includes('UPDATE_PERMISSION')===true?1:0},
                                        DELETE_PERMISSION: ${ checked_roles[role].includes('DELETE_PERMISSION')===true?1:0},
                                        PRINT_PERMISSION: ${ checked_roles[role].includes('PRINT_PERMISSION')===true?1:0},
                                        APPROVE_PERMISSION: ${ checked_roles[role].includes('APPROVE_PERMISSION')===true?1:0},
                                        CREATED_DATE: "${ this.state.CREATED_DATE}",
                                        MODIFIED_DATE: "${ this.state.MODIFIED_DATE}",
                                        CREATED_BY: "${ this.state.CREATED_BY}", 
                                        MODIFIED_BY: "${ this.state.MODIFIED_BY}"
                                    }
                                `
                            );
                        }
                    }

                    const nonCheckedRolesIds = allRolesIds.filter(ROLE_ID => !checkedRolesIds.includes(ROLE_ID));

                    nonCheckedRolesIds.forEach(ROLE_ID => {
                        groupRoles.push(
                            `
                                {
                                    GROUP_ID: ${ GROUP_ID}, 
                                    ROLE_ID: ${ ROLE_ID}, 
                                    ACTIVE: ${ 0}, 
                                    READ_PERMISSION: ${ 0},
                                    CREATE_PERMISSION: ${ 0},
                                    UPDATE_PERMISSION: ${ 0},
                                    DELETE_PERMISSION: ${ 0},
                                    PRINT_PERMISSION: ${ 0},
                                    APPROVE_PERMISSION: ${ 0},
                                    CREATED_DATE: "${ this.state.CREATED_DATE}",
                                    MODIFIED_DATE: "${ this.state.MODIFIED_DATE}",
                                    CREATED_BY: "${ this.state.CREATED_BY}", 
                                    MODIFIED_BY: "${ this.state.MODIFIED_BY}"
                                }
                            `
                        );
                    });

                    const addGroupRoleQuery = `
                        mutation {
                            groupRoleBatchInsert (user_roles: [${ groupRoles}]) {                                    
                                GROUP_ROLE_ID
                            }
                        }
                    `;
                    //console.log('Group Multiple Insert:', addGroupRoleQuery);

                    API.create(addGroupRoleQuery).then(({ data }) => {

                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');

                    }).catch(error => {
                        if (error.response || error.request) {
                            console.error(error);
                            toastr.error('Error Creating Data', 'Failed to create groups');
                        }
                    });

                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        } else {

            let insertGroupRoles = [];
            let updateGroupRoles = [];

            this.state.grouprolesdata.forEach(role => {

                if (role.hasOwnProperty("new")) {
                    insertGroupRoles.push(
                        `
                            {
                                GROUP_ID: ${ this.state.GROUP_ID},
                                ROLE_ID: ${ role.value},
                                ACTIVE: ${ this.state.checkedRoles.includes(role.value + "-READ_PERMISSION")===true?1:0 || this.state.checkedRoles.includes(role.value + "-CREATE_PERMISSION")===true?1:0 || this.state.checkedRoles.includes(role.value + "-UPDATE_PERMISSION")===true?1:0 || this.state.checkedRoles.includes(role.value + "-DELETE_PERMISSION")===true?1:0}, 
                                READ_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-READ_PERMISSION")===true?1:0},
                                CREATE_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-CREATE_PERMISSION")===true?1:0},
                                UPDATE_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-UPDATE_PERMISSION")===true?1:0},
                                DELETE_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-DELETE_PERMISSION")===true?1:0},
                                PRINT_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-PRINT_PERMISSION")===true?1:0},
                                APPROVE_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-APPROVE_PERMISSION")===true?1:0},
                                CREATED_DATE: "${ this.state.CREATED_DATE}",
                                MODIFIED_DATE: "${ this.state.MODIFIED_DATE}",
                                CREATED_BY: "${ this.state.CREATED_BY}", 
                                MODIFIED_BY: "${ this.state.MODIFIED_BY}"
                            }
                        `
                    );
                } else {
                    updateGroupRoles.push(
                        `
                            {
                                GROUP_ROLE_ID: ${ role.value},
                                GROUP_ID: ${ this.state.GROUP_ID},
                                ACTIVE: ${ this.state.checkedRoles.includes(role.value + "-READ_PERMISSION")===true?1:0 || this.state.checkedRoles.includes(role.value + "-CREATE_PERMISSION")===true?1:0 || this.state.checkedRoles.includes(role.value + "-UPDATE_PERMISSION")===true?1:0 || this.state.checkedRoles.includes(role.value + "-DELETE_PERMISSION")===true?1:0}, 
                                READ_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-READ_PERMISSION")===true?1:0},
                                CREATE_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-CREATE_PERMISSION")===true?1:0},
                                UPDATE_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-UPDATE_PERMISSION")===true?1:0},
                                DELETE_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-DELETE_PERMISSION")===true?1:0},
                                PRINT_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-PRINT_PERMISSION")===true?1:0},
                                APPROVE_PERMISSION: ${ this.state.checkedRoles.includes(role.value + "-APPROVE_PERMISSION")===true?1:0},
                                CREATED_DATE: "${ this.state.CREATED_DATE}",
                                MODIFIED_DATE: "${ this.state.MODIFIED_DATE}",
                                CREATED_BY: "${ this.state.CREATED_BY}", 
                                MODIFIED_BY: "${ this.state.MODIFIED_BY}"
                            }
                        `
                    );
                }
            });

            const updateQueries = `
                mutation {
                    updateGroup(
                        GROUP_ID:${this.state.GROUP_ID},
                        GROUP_NAME:"${this.state.GROUP_NAME}",
                        MODIFIED_BY:"${this.state.MODIFIED_BY}",
                        MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        GROUP_ID
                    }
                    
                    groupRoleBatchUpdate (user_roles: [${ updateGroupRoles}]) {                                    
                        GROUP_ROLE_ID
                    }

                    groupRoleBatchInsert (user_roles: [${ insertGroupRoles}]) {                                    
                        GROUP_ROLE_ID
                    }
                }
            `;

            API.update(updateQueries).then(({ data }) => {
                this.clearData();
                this.handleHide();
                this.populateData();
                toastr.info('Update Record', 'Record Updated Successfully');
            }).catch(error => {
                console.error(error)
                if (error.response || error.request) {
                    toastr.error('Error Updating Data', 'Failed to update record');
                }
            })
        }

    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
            mutation{
                deleteGroup(
                    GROUP_ID:${this.state.GROUP_ID}
                ){
                    GROUP_ID
                }
            }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleEditData = (e) => {

        const groupRoles = this.state.allrolegroups.filter(role => role.GROUP_ID === e.GROUP_ID);
        
        const groupRolesIds = groupRoles.map(role => role.ROLE.ROLE_ID);

        let firstRoles = [];
        let lastRoles = [];
        let checkedRoles = [];
        let expandedRoles = [];
        let newRolesRoles = [];

        if (groupRoles.length !== this.state.rolesdata.length) {
            this.state.rolesdata.forEach(role => {
                expandedRoles.push(role.value);
                if (!groupRolesIds.includes(role.value)) {
                    newRolesRoles.push({ ...role, new: true });
                }
            });
        }

        groupRoles.forEach(group_role => {

            let roledetails = {
                value: group_role.GROUP_ROLE_ID,
                label: group_role.ROLE.ROLE_NAME,
                children: [
                    {
                        value: `${group_role.GROUP_ROLE_ID}-READ_PERMISSION`,
                        label: "read"
                    },
                    {
                        value: `${group_role.GROUP_ROLE_ID}-CREATE_PERMISSION`,
                        label: "create"
                    },
                    {
                        value: `${group_role.GROUP_ROLE_ID}-UPDATE_PERMISSION`,
                        label: "update"
                    },
                    {
                        value: `${group_role.GROUP_ROLE_ID}-DELETE_PERMISSION`,
                        label: "delete"
                    },
                    {
                        value: `${group_role.GROUP_ROLE_ID}-PRINT_PERMISSION`,
                        label: "print"
                    },
                    {
                        value: `${group_role.GROUP_ROLE_ID}-APPROVE_PERMISSION`,
                        label: "approve"
                    }
                ],
            };

            if (group_role.READ_PERMISSION) checkedRoles.push(`${group_role.GROUP_ROLE_ID}-READ_PERMISSION`);
            if (group_role.CREATE_PERMISSION) checkedRoles.push(`${group_role.GROUP_ROLE_ID}-CREATE_PERMISSION`);
            if (group_role.UPDATE_PERMISSION) checkedRoles.push(`${group_role.GROUP_ROLE_ID}-UPDATE_PERMISSION`);
            if (group_role.DELETE_PERMISSION) checkedRoles.push(`${group_role.GROUP_ROLE_ID}-DELETE_PERMISSION`);
            if (group_role.PRINT_PERMISSION) checkedRoles.push(`${group_role.GROUP_ROLE_ID}-PRINT_PERMISSION`);
            if (group_role.APPROVE_PERMISSION) checkedRoles.push(`${group_role.GROUP_ROLE_ID}-APPROVE_PERMISSION`);

            group_role.ACTIVE ? firstRoles.push(roledetails) : lastRoles.push(roledetails);

            expandedRoles.push(group_role.GROUP_ROLE_ID);
        });

        const grouprolesdata = [...firstRoles, ...lastRoles, ...newRolesRoles];

        this.setState({
            GROUP_ID: e.GROUP_ID,
            GROUP_NAME: e.GROUP_NAME,
            grouprolesdata,
            checkedRoles,
            expandedRoles,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            GROUP_ID: e.GROUP_ID,
            GROUP_NAME: e.GROUP_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            GROUP_ID: 0,
            GROUP_NAME: '',
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.GROUP_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.GROUP_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    render() {
        const { data, columns, value, filteredData } = this.state
        return (
            <>
                <Helmet><title>Groups</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Security"
                        breadCrumbParent="Groups"
                        breadCrumbActive="Groups"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Groups</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <GroupsTable
                                        role="Groups"
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        columns={columns}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <GroupsModal
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        group_name={this.state.GROUP_NAME}
                                        changegroup_name={e => this.setState({ GROUP_NAME: e.target.value })}
                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                        grouprolesdata={this.state.grouprolesdata}
                                        filteredgrouprolesdata={this.state.filteredgrouprolesdata}

                                        roleFilterText={this.state.roleFilterText}

                                        checkedRoles={this.state.checkedRoles}
                                        expandedRoles={this.state.expandedRoles}

                                        onFilterRolesChange={e => this.setState({ roleFilterText: e.target.value }, this.filterRolesTree)}
                                        handleCheckRole={checkedRoles => this.setState({ checkedRoles })}
                                        handleExpandRole={expandedRoles => this.setState({ expandedRoles })}
                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(Groups));