import React from 'react';
import CheckboxTree from 'react-checkbox-tree';
import { Button, Modal, Form, Col, Row, Card } from 'react-bootstrap';

class GroupsModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Group
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            
                            <Form.Group as={Col} md="6" controlId="group_name">
                                <Form.Label>Group Name</Form.Label>
                                <Form.Control type="text" placeholder="Enter Group Name" value={this.props.group_name} onChange={this.props.changegroup_name} required/>
                            </Form.Group>

                            <Row>
                                <Col className="ml-1 mr-1">
                                    <h6>Employee Roles</h6>
                                    <Card style={{marginTop:'10px', overflowY : 'scroll', maxHeight: '400px'}}>
                                        <Card.Body>

                                            <Form.Group as={Col} md="12" controlId="roleSearch">
                                                <Form.Control
                                                    className="filter-text"
                                                    placeholder="Search Role..."
                                                    type="text"
                                                    value={this.props.roleFilterText}
                                                    onChange={this.props.onFilterRolesChange}
                                                />
                                            </Form.Group>

                                            <CheckboxTree
                                                nodes={this.props.filteredgrouprolesdata.length > 0 ? this.props.filteredgrouprolesdata : this.props.grouprolesdata}
                                                showExpandAll={true}
                                                checked={this.props.checkedRoles}
                                                expanded={this.props.expandedRoles}
                                                onCheck={this.props.handleCheckRole}
                                                onExpand={this.props.handleExpandRole}
                                            />

                                        </Card.Body>
                                    </Card>
                                </Col>
                            </Row>
                            
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Group
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="group_name_del">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.group_name} onChange={this.props.changegroup_name} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default GroupsModal;